<template>
  <div class="container">
    <div v-html="payData" style="display: none;"></div>
    <mu-carousel :hide-controls="true" :hide-indicators="true" class="banner">
      <mu-carousel-item>
        <img src="../assets/lunbo1.png" width="100%">
      </mu-carousel-item>
      <mu-carousel-item>
        <img src="../assets/lunbo3.png" width="100%">
      </mu-carousel-item>
      <mu-carousel-item>
        <img src="../assets/lunbo3.png" width="100%">
      </mu-carousel-item>
    </mu-carousel>

    <div class="list" v-if="orderInfo.productList.length > 0">
      <div class="item" v-for="item,index in orderInfo.productList">

        <div class="ix"> <img :src="item.img" class="ixs"></div>

        <div class="explain">
          <p>{{item.name}}</p>
          <p style="margin: 10px 0;"><span class="pd_left4">规</span> <span>格: </span>{{item.spec}}</p>
          <div class="price"><span class="f_16">¥</span> {{item.dealSum}}</div>
          <!-- <p>生产厂家：{{item.supplyInfo}}</p> -->
          <!-- <p>适用症状：{{item.symptom}}</p> -->
        </div>
        <div class="price-num">
          <!-- <div class="price">¥{{item.dealSum}}</div> -->
          <div class="num">×{{item.count}}{{item.unit}}</div>
        </div>

      </div>
    </div>
    <div v-else class="no-data">{{noDataText}}</div>
    <div class="discounts" v-show="false">
      <div class="discounts_hd">优惠券：</div>
      <div class="discounts_bd">暂无可用优惠券</div>
      <!-- <div class="discounts_ft"><img class="icon" src="../assets/r.png"></div> -->
    </div>
    <div class="payment">
      <div class="info">
        <div><span class="f_36"> ¥ {{orderInfo.paySum}}</span> <span>(优惠金额：¥{{orderInfo.reducedSum}})</span></div>

      </div>
      <!-- <mu-button @click="getData(true)" data-mu-loading-size="24" data-mu-loading-color="#4caf50" v-loading="loading">确认付款</mu-button> -->
       <mu-button @click="status()" data-mu-loading-size="24" data-mu-loading-color="#4caf50" v-loading="loading">确认付款</mu-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import wx from 'weixin-js-sdk'

function GetQueryString(name)
{
  var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
  var r = window.location.search.substr(1).match(reg);
  if(r!=null)return  unescape(r[2]); return null;
}

export default {
  //appid wxbba4b3d497457d6b 正式
  //appid wx2457d1288d0e6877 测试
  data () {
    return {
      discounts: '',
      orderId: '',
      //host: 'http://leagoohealth.com:18081',//正式
      host: 'http://test.leagoohealth.com:18081',//测试
      orderInfo: {
        productList:'',
        paySum: '',
        reducedSum: 0,
      },
      noDataText: '',
      payData: '',
      loading: false,
    }
  },
  created () {
    if (/MicroMessenger/.test(window.navigator.userAgent)) {
      if (window.location.href.split('?').length == 3) {
        this.code = window.location.href.split('?')[1].split('#')[0].split('&')[0].split('=')[1]
        this.orderId = window.location.href.split('?')[2].split('=')[1]
        this.getData()
      } else {
         //正式
//         window.location.replace('https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + this.$route.query.appId + '&redirect_uri=http%3a%2f%2fwww.leagoohealth.com%2fpay%2f%23%2findex?orderId=' + this.$route.query.orderId + '&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect')
       //测试
       window.location.replace('https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + this.$route.query.appId + '&redirect_uri=http%3a%2f%2fwww.leagoohealth.com%2fpaytest%2f%23%2findex?orderId=' + this.$route.query.orderId + '&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect')
     }
    }
//    else if(/Bestpay/.test(window.navigator.userAgent) &&  GetQueryString("resultCode")){
//        const resultCode = GetQueryString("resultCode");
//        alert("结果：" + resultCode);
//        const result = GetQueryString("result");
//        if(resultCode == "-1"){
//            this.$router.replace({ path:'/success' })
//        }else {
//            this.$router.replace({path:'/error'})
//        }
//    }
    else {
      this.orderId = this.$route.query.orderId
      this.getData()
    }
  },
  mounted () {

  },
  updated () {

  },
  methods: {
    toPay () { //调起支付
      this.loading = true
      let self = this
      if (/MicroMessenger/.test(window.navigator.userAgent)){
        axios({
          url: self.host + '/leagoo-health-api/api/pay/wxOrder',
          method: 'post',
          data: {
            orderId: self.orderId,
            code: self.code
          },
        }).then(response => {
          if (response.data.code == 200) {
            wx.config({
              debug: false,
              appId: response.data.data.appId,
              timestamp: response.data.data.timeStamp,
              nonceStr: response.data.data.nonceStr,
              signature: 'MD5',
              jsApiList: [ "chooseWXPay", "closeWindow" ]
            })
            wx.ready(() => {
              wx.chooseWXPay({
                appId: response.data.data.appId,
                timestamp: response.data.data.timeStamp,
                nonceStr: response.data.data.nonceStr,
                package: response.data.data.package,
                signType: response.data.data.signType,
                paySign: response.data.data.paySign,
                success: function(res) {
                  self.loading = false
                  if (res.errMsg == "chooseWXPay:ok") {
                    self.$router.replace({ path:'/success' })
                  } else {
                    self.$router.replace({ path:'/error' })
                  }
                },
                cancel: function(res) {
                  self.loading = false
                  wx.closeWindow()
                }
              })
            })
          }
        }, error =>{
          self.loading = false

          console.log(error)
        })
      } else if (/AlipayClient/.test(window.navigator.userAgent)) {
        axios({
          url: self.host + '/leagoo-health-api/api/pay/aliOrder',
          method: 'post',
          data: {
            orderId: self.orderId
          },
        }).then(response => {
          self.loading = false
          if (response.data.code == 200) {
            self.payData = response.data.form
            self.$nextTick(() => {
              document.forms[0].submit()
            })
          } else {

          }
        }, error => {
          console.log(error)
        })
      }else if(/Bestpay/.test(window.navigator.userAgent)){
          axios({
            url: self.host + '/leagoo-health-api/api/pay/telecomOrder',
            method: 'post',
            data: {
              orderId: self.orderId
            },
          }).then(response =>{
            self.loading = false
            if (response.data.code == 200) {
              BestpayHtml5.config();
              self.getProductNo(response.data.data);
            }
          },error =>{
            console.log(error);
          });
      }
    },
    getProductNo(serverData){
      const self = this;
      BestpayHtml5.User.getProductNo({},
        function (obj) {
          self.doBastpay(serverData ,obj.productNo);
        }, function (obj) {
          console.log(obj);
        }, function (obj) {
          console.log(obj);
        })
    },
    doBastpay(serverData ,productNo){
        alert(JSON.stringify(serverData));
        alert(JSON.stringify(productNo));
      const payJson = {
        "INSTITUTIONCODE":serverData.merchanttd,
        "INSTITUTIONTYPE":"MERCHANT",
        "SIGNTYPE":"CA",
        "SIGN":serverData.sign,
        "SERVICE":"mobile.security.pay",
        "MERCHANTID":serverData.merchanttd,
        "SUBMERCHANTID":"deprecated",
        "BACKMERCHANTURL":"deprecated",
        "ORDERSEQ":serverData.tradeprodNo,
        "ORDERREQTRANSEQ":serverData.orderId,
        "TRADENO":serverData.tradeNo,
        "ORDERTIME":"deprecated",
        "ORDERVALIDITYTIME":"deprecated",
        "ORDERAMOUNT":this.orderInfo.paySum,
        "CURTYPE":"156",
        "PRODUCTID":"04",
        "PRODUCTDESC":"领歌云康设备端支付",
        "PRODUCTAMOUNT":this.orderInfo.paySum,
        "ATTACHAMOUNT":"0.00",
        "BUSITYPE":"04",
        "SUBJECT":"领歌云康设备端支付",
        "ACCOUNTID": productNo
      };
      const self = this;
      const success = () => {
        alert("拉起成功！11111111");
        self.$router.replace({ path:'/success' })
      };
      const fail = (rescode) => {
        alert("拉起失败！22222222" + JSON.stringify(rescode));
        self.$router.replace({path:'/error'})
      }
      alert(JSON.stringify(payJson));
      BestpayHtml5.Payment.pay(payJson, success, fail);
    },
    getData (pay) { //然后再查询订单属性
      let self = this;
      axios({
        url: self.host + '/leagoo-health-api/api/order/orderDetail',
        method: 'post',
        data: {
          orderId: self.orderId
        },
      }).then(response => {
        if (response.data.code == 200) {
          console.log(response.data.code);
          self.orderInfo = response.data.data
          if (pay) {
            self.toPay()
          }
        } else {
          self.noDataText = '暂无订单信息'
        }
      }, error => {
        console.log(error)
      })
    },
    status(){ //首先查询 订单id 是否存在
      let self=this;
       axios({
        url:self.host + '/leagoo-health-api/api/order/checkOrderState',
        method:'post',
        data:{
          orderId:self.orderId
        },
      }).then(res=>{
        console.log(res);

					if(res.data.code== 200){
            console.log('订单已成功' + res)
          self.getData(true)
          }

          if(res.data.code==30002){
            alert("订单已失效,请重新扫码二维码支付")
              wx.closeWindow()
          }

      }).catch(err=>{
          alert('订单已取消');
          wx.closeWindow()
      })
    },
  }
}
</script>

<style lang="less" scoped>
.container{
  width:100%;
  height:100%;
  position:fixed;
 //background :url('../assets/di.png');
  background: #142d26;
  background-size:100%;
  overflow-y: scroll;

.list{
  margin-top:6px ;margin-bottom:62px;
  .item{
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    align-items: center;
    margin:0px 15px;
    padding:10px;
    letter-spacing: 1px;
    // background: #030807;
   // border-bottom: 1px solid #eee;
    background:url('../assets/bg.png');
    img{
      display: block;
      width: 80%;
      height: 68px;
      padding-top: 10px;
      margin-top: 5px;
      margin-left: 10px;
    }

    .explain{
      margin: 0 20px;
      flex: 1;
      color: #fff;
      .pd_left4{padding-right:18px;}
      p{
        margin: 0;
        font-size: 13px;
      }
      .price{
        color: #FFEC17;
        font-size: 16px;
      }
    }
    .price-num{
      text-align: center;
      color: #fff;
      .price{
        color: #fff;
      }
    }
  }
}
.banner{
  height: 175px;
}
.payment{
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  height: 56px;
  background:url('../assets/footbg.png');
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 20px;
  .info{
    color: #fff;
    div:first-child{
      font-size: 18px;
    }
    div:last-child{
      font-size: 12px;
    }
  }
  .mu-button{
            background: rgb(35, 94, 29);
            color: #fff;
    }

}
.container{
  margin-bottom: 56px;
}
.discounts{
  display: flex;
  justify-content: space-between;
  height: 52px;
  font-size: 14px;
  align-items: center;
  padding: 0 10px;
  color: #fff;

  background:url('../assets/bg.png');
   margin:0px 15px;
  .discounts_bd{
    flex: 1;
    text-align: right;
    color: #fff;

  }
  .discounts_ft{
    .icon{
      display: block;
      width: 16px;
    }
  }
}
.no-data{
 background: #020807;
  line-height: 50px;
  text-align: center;
  color: #fff;
}

.mu-button-wrapper{background:url('../assets/but.png') no-repeat;background:100%;}
.f_36{font-size:18px;}

.discounts{margin-bottom: 66px;}

    .f_16{font-size:12px;}
      }

 .ix{width:80px;height:80px;}
 .ixs{width:90%;height:100%;}
</style>



